import { WebSocketClientEvent } from '@brandembassy/push-updates-sdk';
import { AuthorizationToken } from '@brandembassy/ts-types-chat/dist/authorization/AuthorizationToken';
import { ConsumerAuthorizationSuccessPayloadData } from '@brandembassy/ts-types-chat/dist/authorization/ConsumerAuthorizationSuccessPayloadData';
import { AwsChatWindowEventActionRoute } from '@brandembassy/ts-types-chat/dist/awsEvents/AwsChatWindowEvent';
import { AwsInputEventType } from '@brandembassy/ts-types-chat/dist/awsEvents/AwsEventType';
import { IThread } from '@brandembassy/ts-types-chat/dist/post/thread/IThread';
import { BrandId } from '@brandembassy/ts-types-platform/dist/brand/BrandId';
import { ChannelId } from '@brandembassy/ts-types-platform/dist/channel/ChannelId';
import { CustomerIdentityIdOnExternalPlatform } from '@brandembassy/ts-types-platform/dist/endUser/EndUserIdentityId';
import { ThreadIdOnExternalPlatform } from '@brandembassy/ts-types-platform/dist/thread/ThreadIdOnExternalPlatform';

import {
  getAccessToken,
  setAccessToken,
} from './authorization/accessTokenStorage';
import { createAuthorizationPayloadData } from './authorization/authorizationPayloadData';
import { createRefreshTokenPayload } from './authorization/createRefreshTokenPayload';
import { createGenerateAuthorizationTokenPayloadData } from './authorization/generateAuthorizationTokenPayload';
import { handleRefreshToken } from './authorization/handleRefreshToken';
import { isAuthorizationTokenGeneratedPostbackData } from './authorization/isAuthorizationTokenGeneratedPostbackData';
import { isAuthSuccessEvent } from './authorization/isAuthSuccessEvent';
import { isTokenRefreshedSuccessResponse } from './authorization/isTokenRefreshedSuccessResponse';
import { ChatSDKOptions } from './ChatSDKOptions';
import { Customer } from './customer/customer';
import { ChatSDKVariable } from './env/ChatSDKVariable';
import { EnvironmentName, getEnvironmentEndpoints } from './env/Environments';
import { SDKVariableStorage } from './env/getEnvVariable';
import { ChatSDKError } from './error/ChatSDKError';
import { ChatCustomEvent, ChatEventType } from './event/ChatEvent';
import { ChatEventMiddleware } from './event/ChatEventMiddleware';
import { ChatEventTarget, IChatEventTarget } from './event/ChatEventTarget';
import { RemoveListenerFunction } from './event/ListenerFunction';
import { chatEventMapper } from './event/middleware/ChatEventMapper';
import { sendChatEvent } from './event/sendChatEvent';
import { MessageSuccessEventData } from './message/MessageSuccessEventData';
import {
  OfflineMessageData,
  sendOfflineMessage,
} from './message/sendOfflineMessage';
import { makeRequest } from './request/makeRequest';
import {
  onMessageHandler,
  parseWsMessageEvent,
} from './request/messageHandler';
import { createEvent, createEventPayload } from './sockets/EventPayload';
import {
  WebSocketClient,
  WebSocketClientOptions,
} from './sockets/WebSocketClient';
import { LivechatThread, Thread } from './thread';
import {
  isRecoverSuccessPayload,
  ThreadRecoveredChatEvent,
} from './thread/isRecoverSuccessPayload';
import { isThreadListFetchedPostbackData } from './thread/isThreadListFetchedPostbackData';
import { createRecoverLivechatThreadPayloadData } from './thread/livechatThread/recoverLivechatThreadPayload';
import { createRecoverThreadPayloadData } from './thread/recoverThreadPayload';
import { storeContactIdMiddleware } from './thread/storeContactIdMiddleware';
import { storeThreadScrollDataMiddleware } from './thread/storeThreadScrollDataMiddleware';
import { assertNonNullable } from './utils/assert';
import { generateId } from './utils/generateId';
import { getBrandIdAndChannelId } from './utils/getBrandIdAndChannelId';
import { isEmpty, isNil, isNull, isUndefined } from './utils/is';

export class ChatSdk {
  onError?: (error: Error) => void;
  onRawEvent?: (event: ChatCustomEvent) => void;
  public isLivechat: boolean = false;
  public channelId: ChannelId = '';
  private websocketClient: WebSocketClient | null = null;
  private customer: Customer | null = null;
  private _incomingChatEventMiddleware = new ChatEventMiddleware();
  private _messageEmitter: IChatEventTarget;
  private isAuthorizationEnabled: boolean = false;
  private _threadCache: Map<
    ThreadIdOnExternalPlatform,
    Thread | LivechatThread
  > = new Map();

  constructor(options: ChatSDKOptions) {
    if (options === undefined) {
      throw new Error('No options was provided for initialization of ChatSdk');
    }

    SDKVariableStorage.set(
      ChatSDKVariable.AUTHORIZATION_CODE,
      options.authorizationCode,
    );
    SDKVariableStorage.set(ChatSDKVariable.BRAND_ID, `${options.brandId}`);
    SDKVariableStorage.set(ChatSDKVariable.CHANNEL_ID, options.channelId);

    const { brandId, channelId } = getBrandIdAndChannelId();

    this.onError = options.onError;
    this.onRawEvent = options.onRawEvent;

    // Register middleware for incoming chat events
    this._incomingChatEventMiddleware.register(chatEventMapper);
    this._incomingChatEventMiddleware.register(storeThreadScrollDataMiddleware);
    this._incomingChatEventMiddleware.register(storeContactIdMiddleware);

    this._messageEmitter = new ChatEventTarget();

    try {
      if (isNaN(brandId)) {
        throw new Error('Missing BrandID');
      }
      if (channelId === undefined) {
        throw new Error('Missing ChannelId');
      }
      if (options.customerId === undefined) {
        throw new Error('Missing CustomerId');
      }

      this._initEnvironment(options);
      this._initWS(brandId, channelId, options.customerId);
      // Init Customer instance after the websocketClient is initialized
      this.customer = new Customer(
        options.customerId,
        options.customerName,
        this.websocketClient,
      );
      this.channelId = channelId;
    } catch (error: unknown) {
      this.onErrorHandler(error);
    }
  }

  onErrorHandler(error: unknown): void {
    if (typeof this.onError === 'function') {
      this.onError(new ChatSDKError(error));
    } else {
      throw new ChatSDKError(error);
    }
  }

  /**
   * Send Authorization Event
   * @param authorizationCode - authorization code
   * @param visitorId - visitor id
   */
  public async authorize(
    authorizationCode?: string,
    visitorId?: string,
  ): Promise<ConsumerAuthorizationSuccessPayloadData> {
    const authorizationPayloadData = createAuthorizationPayloadData(
      authorizationCode ??
        SDKVariableStorage.get(ChatSDKVariable.AUTHORIZATION_CODE, null),
      visitorId,
    );

    const event = createEvent(
      createEventPayload(authorizationPayloadData),
      generateId(),
      AwsChatWindowEventActionRoute.REGISTER,
    );

    const response = await makeRequest(event, this.websocketClient);

    if (isAuthSuccessEvent(response)) {
      this.isLivechat = response.data?.channel?.settings?.isLivechat ?? false;
      this.isAuthorizationEnabled =
        response.data.channel?.settings.isAuthorizationEnabled ?? false;

      if (response.data.accessToken?.token !== undefined) {
        setAccessToken(response.data.accessToken);

        if (
          response.data?.consumerIdentity?.firstName !== undefined ||
          response.data?.consumerIdentity?.lastName !== undefined
        ) {
          this.customer?.setName(
            `${response.data.consumerIdentity.firstName} ${response.data.consumerIdentity.lastName}`,
          );
        }

        handleRefreshToken(
          response.data.accessToken,
          this._sendRefreshTokenEvent,
        );
      }

      return response.data;
    }

    throw response;
  }

  /**
   * Generate Authorization Token from the given url
   *
   * @param threadIdOnExternalPlatform - Thread Id
   * @param url - Authorization Service URL
   */
  public async generateAuthorizationToken(
    threadIdOnExternalPlatform: ThreadIdOnExternalPlatform,
    url: string,
  ): Promise<AuthorizationToken> {
    const result = await sendChatEvent(
      createGenerateAuthorizationTokenPayloadData(
        threadIdOnExternalPlatform,
        url,
      ),
      this.websocketClient,
    );

    if (!isAuthorizationTokenGeneratedPostbackData(result.data)) {
      throw new ChatSDKError(
        'Invalid response from generate authorization token (generateAuthorizationToken)',
      );
    }

    const { authorizationToken } = result.data;

    return authorizationToken;
  }

  /**
   * Register handler to chat event
   *
   * @param type - type of chat event
   * @param handler - event handler
   * @returns function to unregister handler
   */
  public onChatEvent(
    type: ChatEventType,
    handler: (event: ChatCustomEvent) => void,
  ): RemoveListenerFunction {
    this._messageEmitter.addEventListener(type, handler);

    return () => {
      this._messageEmitter.removeEventListener(type, handler);
    };
  }

  /**
   * Get Customer instance
   */
  public getCustomer(): Customer | null {
    return this.customer;
  }

  /**
   * Get Thread instance by id
   * @param id - thread id
   * @returns instance of thread based on channel settings
   */
  public getThread(id: ThreadIdOnExternalPlatform): Thread | LivechatThread {
    if (isNull(this.websocketClient)) {
      throw new ChatSDKError(
        'Cannot get thread because websocket is disconnected',
      );
    }

    if (isNil(id)) {
      throw new ChatSDKError('Cannot get thread because id is undefined');
    }

    const cachedThread = this._threadCache.get(id);
    if (!isUndefined(cachedThread)) {
      return cachedThread;
    }

    if (this.isLivechat) {
      const liveThread = new LivechatThread(
        id,
        this.websocketClient,
        this._messageEmitter,
        this.isAuthorizationEnabled,
      );
      this._threadCache.set(id, liveThread);

      return liveThread;
    }

    const thread = new Thread(
      id,
      this.websocketClient,
      this._messageEmitter,
      this.isAuthorizationEnabled,
    );
    this._threadCache.set(id, thread);

    return thread;
  }

  /**
   * Get list of available threads
   * @returns list of threads
   */
  public async getTheadList(): Promise<Array<IThread> | null> {
    if (isNull(this.websocketClient)) {
      throw new ChatSDKError(
        'Cannot get thread list because websocket is disconnected',
      );
    }

    const eventPayload = {
      eventType: AwsInputEventType.FETCH_THREAD_LIST,
      data: {},
    };

    const result = await sendChatEvent(eventPayload, this.websocketClient);

    if (!isThreadListFetchedPostbackData(result.data)) {
      throw new ChatSDKError(
        'Invalid response from fetch thread list (getTheadList)',
      );
    }

    return result.data.threads;
  }

  /**
   * Get access to a websocket connection
   * @returns WebSocketClient instance
   */
  public getWebsocketClient(): WebSocketClient | null {
    return this.websocketClient;
  }

  /**
   * Send the Offline Message
   * @param offlineMessageData - offline message data (name, email, message)
   * @returns success
   */
  public async sendOfflineMessage(
    offlineMessageData: OfflineMessageData,
  ): Promise<MessageSuccessEventData> {
    return sendOfflineMessage(offlineMessageData, this.websocketClient);
  }

  private _sendRefreshTokenEvent = async (): Promise<void> => {
    const accessToken = getAccessToken();
    if (isNull(accessToken)) {
      return;
    }

    const refreshTokenResponse = await sendChatEvent(
      createRefreshTokenPayload(accessToken.token),
      this.websocketClient,
    );

    if (isTokenRefreshedSuccessResponse(refreshTokenResponse)) {
      setAccessToken(refreshTokenResponse.data.accessToken);

      handleRefreshToken(
        refreshTokenResponse.data.accessToken,
        this._sendRefreshTokenEvent,
      );

      return;
    }

    throw refreshTokenResponse;
  };

  /**
   * Recover thread data
   * @param threadIdOnExternalPlatform - thread id on external platform
   * @returns thread session data
   */
  public async recoverThreadData(
    threadIdOnExternalPlatform:
      | ThreadIdOnExternalPlatform
      | undefined = undefined,
  ): Promise<ThreadRecoveredChatEvent> {
    const recoverEvent = createRecoverThreadPayloadData(
      threadIdOnExternalPlatform,
    );
    const recoverResponse = await sendChatEvent(
      recoverEvent,
      this.websocketClient,
    );

    if (!isRecoverSuccessPayload(recoverResponse)) {
      throw new ChatSDKError('Invalid response from recover livechat thread');
    }

    return recoverResponse;
  }

  /**
   * Recover livechat thread data
   * @param threadIdOnExternalPlatform - thread id on external platform
   * @returns thread livechat session data
   */
  public async recoverLivechatThreadData(
    threadIdOnExternalPlatform:
      | ThreadIdOnExternalPlatform
      | undefined = undefined,
  ): Promise<ThreadRecoveredChatEvent> {
    const recoverEvent = createRecoverLivechatThreadPayloadData(
      threadIdOnExternalPlatform,
    );
    const recoverResponse = await sendChatEvent(
      recoverEvent,
      this.websocketClient,
    );

    if (!isRecoverSuccessPayload(recoverResponse)) {
      throw new ChatSDKError('Invalid response from recover livechat thread');
    }

    return recoverResponse;
  }

  /**
   * Setup Environment endpoints
   */
  private _initEnvironment(options: ChatSDKOptions): void {
    // Set custom environment
    if (options.environment === EnvironmentName.custom) {
      if (isEmpty(options.customEnvironment)) {
        throw new Error(
          'customEnvironment must be provided when environment is set to "custom"',
        );
      }
      SDKVariableStorage.set(
        ChatSDKVariable.ENDPOINT_GATEWAY,
        options.customEnvironment?.gateway,
      );
      SDKVariableStorage.set(
        ChatSDKVariable.ENDPOINT_CHAT,
        options.customEnvironment?.chat,
      );

      return;
    }

    // Set predefined environment
    const { gateway, chat } = getEnvironmentEndpoints(options.environment);
    SDKVariableStorage.set(ChatSDKVariable.ENDPOINT_GATEWAY, gateway);
    SDKVariableStorage.set(ChatSDKVariable.ENDPOINT_CHAT, chat);
  }

  private _initWS(
    brandId: BrandId,
    channelId: ChannelId,
    customerId: CustomerIdentityIdOnExternalPlatform,
  ): void {
    const endpoint = SDKVariableStorage.get(ChatSDKVariable.ENDPOINT_GATEWAY);
    assertNonNullable(endpoint);

    const url = new URL(endpoint);
    const protocol = url.protocol;
    const host = url.hostname;
    const port = url.port;
    const prefix = url.pathname.substring(1);
    const forceSecureProtocol = protocol === 'wss:';

    const wsClientOptions: WebSocketClientOptions = {
      host,
      port,
      prefix,
      forceSecureProtocol,
    };
    this.websocketClient = new WebSocketClient(
      brandId,
      channelId,
      customerId,
      wsClientOptions,
      this.onError,
    );

    this.websocketClient.on(
      WebSocketClientEvent.MESSAGE,
      (event: ChatCustomEvent) => {
        try {
          if (typeof this.onRawEvent === 'function') {
            this.onRawEvent(event);
          }

          const parsedEvent = this._incomingChatEventMiddleware.process(
            parseWsMessageEvent(event),
          );

          if (!isNil(parsedEvent)) {
            const { type } = parsedEvent;

            onMessageHandler(parsedEvent);

            this._messageEmitter.dispatchEvent(
              new ChatCustomEvent(type ?? '', {
                detail: parsedEvent,
              }),
            );
          }
        } catch (error: unknown) {
          this.onErrorHandler(error);
        }
      },
    );
  }
}
