import { ChatSDKVariable } from './ChatSDKVariable';

class VariablesStorage {
  private _vars: Partial<Record<ChatSDKVariable, string | undefined>> = {};

  set(variableName: ChatSDKVariable, value: string | undefined): void {
    this._vars[variableName] = value;
  }

  get<D = undefined>(
    variableName: ChatSDKVariable,
    defaultValue?: D,
  ): string | D {
    return this._vars[variableName] ?? (defaultValue as D);
  }

  list() {
    return Object.keys(this._vars);
  }

  clear() {
    this._vars = {};
  }
}

export const SDKVariableStorage = new VariablesStorage();
