import { CustomField } from '@brandembassy/ts-types-platform/dist/customField/CustomField';

import { uniqBy } from '../utils/uniq';

import { convertCustomFields } from './convertCustomField';
import { CustomFields } from './CustomFields';

export const mergeCustomFields = (
  customFieldsQueue: CustomFields,
  dataCustomFields: Array<CustomField>,
): Array<CustomField> => {
  const convertedQueue = convertCustomFields(customFieldsQueue);

  return uniqBy([...dataCustomFields, ...convertedQueue], 'ident');
};
