import { AwsInputEventType } from '@brandembassy/ts-types-chat/dist/awsEvents/AwsEventType';
import { SendConsumerCustomFieldsEventData } from '@brandembassy/ts-types-chat/dist/awsEvents/SendConsumerCustomFields';

import { convertCustomFields } from '../customField/convertCustomField';
import { CustomFields } from '../customField/CustomFields';
import { EventPayloadData } from '../sockets/EventPayload';

type SendCustomerCustomFieldsEventData = SendConsumerCustomFieldsEventData;

export function createSetCustomerCustomFieldsPayloadData(
  customFields: CustomFields,
): EventPayloadData<SendCustomerCustomFieldsEventData> {
  return {
    eventType: AwsInputEventType.SEND_CONSUMER_CUSTOM_FIELDS,
    data: {
      customFields: convertCustomFields(customFields),
    },
  };
}
