import { AuthorizeConsumerData } from '@brandembassy/ts-types-chat/dist/awsEvents/AuthorizeConsumer';
import { AwsInputEventType } from '@brandembassy/ts-types-chat/dist/awsEvents/AwsEventType';
import { VisitorId } from '@brandembassy/ts-types-platform/dist/visitor/VisitorId';

import { EventPayloadData } from '../sockets/EventPayload';
import { generateId } from '../utils/generateId';
import { createVisitorPayload } from '../visitor/createVisitorPayload';

type AuthorizeCustomerData = AuthorizeConsumerData;

export function createAuthorizationPayloadData(
  authorizationCode: string | null,
  visitorId: VisitorId = generateId(),
): EventPayloadData<AuthorizeCustomerData> {
  return {
    eventType: AwsInputEventType.AUTHORIZE_CONSUMER,
    data: {
      authorization: {
        authorizationCode,
      },
    },
    ...createVisitorPayload(visitorId as VisitorId),
  };
}
