import { CaseId } from '@brandembassy/ts-types-platform/dist/post/case/CaseId';

import { ChatSDKVariable } from '../env/ChatSDKVariable';
import { SDKVariableStorage } from '../env/getEnvVariable';

export function storeContactId(contactId: CaseId): void {
  const originalThreadDataRaw = SDKVariableStorage.get(
    ChatSDKVariable.THREAD_DATA,
    '{}',
  );
  const originalThreadData = JSON.parse(originalThreadDataRaw) || {};

  SDKVariableStorage.set(
    ChatSDKVariable.THREAD_DATA,
    JSON.stringify({
      ...originalThreadData,
      contactId,
    }),
  );
}
