export enum EnvironmentName {
  AU1 = 'AU1',
  CA1 = 'CA1',
  EU1 = 'EU1',
  JP1 = 'JP1',
  NA1 = 'NA1',
  UK1 = 'UK1',
  custom = 'custom',
}

export interface EnvironmentEndpoints {
  chat: string;
  gateway: string;
  name: string;
}

export function getEnvironmentEndpoints(
  environment: EnvironmentName,
): EnvironmentEndpoints {
  const placeholder = '{ENV}';
  const chatUrlTemplate = `https://channels-de-${placeholder}.niceincontact.com`;
  const gatewayUrlTemplate = `wss://chat-gateway-de-${placeholder}.niceincontact.com`;

  switch (environment) {
    case EnvironmentName.AU1:
      return {
        chat: chatUrlTemplate.replace(placeholder, 'au1'),
        name: 'Australia',
        gateway: gatewayUrlTemplate.replace(placeholder, 'au1'),
      };
    case EnvironmentName.CA1:
      return {
        chat: chatUrlTemplate.replace(placeholder, 'ca1'),
        name: 'Canada',
        gateway: gatewayUrlTemplate.replace(placeholder, 'ca1'),
      };
    case EnvironmentName.EU1:
      return {
        chat: chatUrlTemplate.replace(placeholder, 'eu1'),
        name: 'Europe',
        gateway: gatewayUrlTemplate.replace(placeholder, 'eu1'),
      };
    case EnvironmentName.JP1:
      return {
        chat: chatUrlTemplate.replace(placeholder, 'jp1'),
        name: 'Japan',
        gateway: gatewayUrlTemplate.replace(placeholder, 'jp1'),
      };
    case EnvironmentName.NA1:
      return {
        chat: chatUrlTemplate.replace(placeholder, 'na1'),
        name: 'North America',
        gateway: gatewayUrlTemplate.replace(placeholder, 'na1'),
      };
    case EnvironmentName.UK1:
      return {
        chat: chatUrlTemplate.replace(placeholder, 'uk1'),
        name: 'United Kingdom',
        gateway: gatewayUrlTemplate.replace(placeholder, 'uk1'),
      };
    case EnvironmentName.custom:
      return {
        chat: '',
        name: 'Custom',
        gateway: '',
      };
    default:
      throw new Error(`Unknown environment: ${environment}`);
  }
}
