import { AwsInputEventType } from '@brandembassy/ts-types-chat/dist/awsEvents/AwsEventType';
import { SetConsumerContactCustomFieldsEventData } from '@brandembassy/ts-types-chat/dist/awsEvents/SetConsumerContactCustomFields';
import { ThreadIdOnExternalPlatform } from '@brandembassy/ts-types-platform/dist/thread/ThreadIdOnExternalPlatform';

import { convertCustomFields } from '../customField/convertCustomField';
import { CustomFields } from '../customField/CustomFields';
import { EventPayloadData } from '../sockets/EventPayload';

export function createSetThreadCustomFieldsPayloadData(
  customFields: CustomFields,
  threadIdOnExternalPlatform: ThreadIdOnExternalPlatform,
): EventPayloadData<SetConsumerContactCustomFieldsEventData> {
  return {
    eventType: AwsInputEventType.SET_CONSUMER_CONTACT_CUSTOM_FIELD,
    data: {
      customFields: convertCustomFields(customFields),
      thread: {
        idOnExternalPlatform: threadIdOnExternalPlatform,
      },
    },
  };
}
