import { ChatEvent, ChatEventData } from '../event/ChatEvent';

import { ContactStatusChangedChatEvent } from './isContactToRoutingQueueAssignmentChangedEvent';

export function contactToRoutingQueueAssignmentChanged(
  event: ChatEventData,
): ContactStatusChangedChatEvent {
  return {
    ...(event as ContactStatusChangedChatEvent),
    type: ChatEvent.CONTACT_TO_ROUTING_QUEUE_ASSIGNMENT_CHANGED,
  };
}
