import { SendMessageEventData } from '@brandembassy/ts-types-chat/dist/awsEvents/SendMessage';
import { ThreadRecoveredPostbackData } from '@brandembassy/ts-types-chat/dist/awsEvents/ThreadRecovered';
import { CaseStatus } from '@brandembassy/ts-types-platform/dist/post/case/CaseStatus';
import { ThreadIdOnExternalPlatform } from '@brandembassy/ts-types-platform/dist/thread/ThreadIdOnExternalPlatform';

import { isContactStatusChangedEvent } from '../../contact';
import { ChatSDKVariable } from '../../env/ChatSDKVariable';
import { SDKVariableStorage } from '../../env/getEnvVariable';
import { ChatSDKError } from '../../error/ChatSDKError';
import { ChatCustomEvent, ChatEvent } from '../../event/ChatEvent';
import { IChatEventTarget } from '../../event/ChatEventTarget';
import { sendChatEvent } from '../../event/sendChatEvent';
import { MessageSuccessEventData } from '../../message/MessageSuccessEventData';
import { WebSocketClient } from '../../sockets/WebSocketClient';
import { isUndefined } from '../../utils/is';
import { isRecoverSuccessPayload } from '../isRecoverSuccessPayload';
import { Thread } from '../Thread';

import { createEndThreadPayloadData } from './createEndThreadPayloadData';
import { createRecoverLivechatThreadPayloadData } from './recoverLivechatThreadPayload';

export class LivechatThread extends Thread {
  protected _isInitialized: boolean = false;
  protected _canSendMessage: boolean = true;
  constructor(
    idOnExternalPlatform: ThreadIdOnExternalPlatform,
    websocketClient: WebSocketClient,
    messageEmitter: IChatEventTarget,
    isAuthorizationEnabled: boolean = false,
  ) {
    super(
      idOnExternalPlatform,
      websocketClient,
      messageEmitter,
      isAuthorizationEnabled,
    );

    this._registerLivechatEventHandlers();
  }
  /**
   *  Recover existing live chat
   */
  public override async recover(): Promise<ThreadRecoveredPostbackData> {
    const response = await sendChatEvent(
      createRecoverLivechatThreadPayloadData(this.idOnExternalPlatform),
      this._websocketClient,
    );

    if (isRecoverSuccessPayload(response)) {
      return response.data;
    }

    throw response;
  }

  public override async sendMessage(
    messageData: SendMessageEventData,
  ): Promise<MessageSuccessEventData> {
    if (this._canSendMessage === false) {
      throw new ChatSDKError('Cannot send more messages to closed Contact');
    }

    return super.sendMessage(messageData);
  }

  /**
   * Start livechat
   * @param initialMessageText - initial message text
   */
  public async startChat(
    initialMessageText = 'Begin conversation',
  ): Promise<MessageSuccessEventData | void> {
    if (this._isInitialized) {
      throw new ChatSDKError('Chat is already initialized');
    }

    try {
      const result = await this.sendTextMessage(initialMessageText);
      this._isInitialized = true;

      return result;
    } catch (error: unknown) {
      if (error instanceof Error) {
        throw new ChatSDKError(
          `Sending initial message failed because of (${error.message})`,
        );
      }

      return;
    }
  }

  public async endChat(): Promise<void> {
    const threadDataRaw = SDKVariableStorage.get(
      ChatSDKVariable.THREAD_DATA,
      '{}',
    );
    const threadData = JSON.parse(threadDataRaw);
    const contactId = threadData?.contactId;

    if (isUndefined(contactId)) {
      throw new ChatSDKError(
        'Cannot end Chat because of missing ContactId in the storage',
      );
    }

    await sendChatEvent(
      createEndThreadPayloadData(this.idOnExternalPlatform, contactId),
      this._websocketClient,
    );

    return;
  }

  private _registerLivechatEventHandlers() {
    this.onThreadEvent(
      ChatEvent.CONTACT_STATUS_CHANGED,
      (event: ChatCustomEvent) => {
        if (
          isContactStatusChangedEvent(event.detail) &&
          event.detail.data.case.status === CaseStatus.CLOSED
        ) {
          this._canSendMessage = false;
        }
      },
    );
    this.onThreadEvent(
      ChatEvent.LIVECHAT_RECOVERED,
      (event: ChatCustomEvent) => {
        if (!isRecoverSuccessPayload(event.detail)) {
          return;
        }

        this._exists = true;

        const status =
          event.detail.data.consumerContact?.status ??
          event.detail.data.contact?.status;

        if (status === CaseStatus.CLOSED) {
          this._canSendMessage = false;
        }
      },
    );
  }
}
