import { CustomField } from '@brandembassy/ts-types-platform/dist/customField/CustomField';
import { CustomerIdentityIdOnExternalPlatform } from '@brandembassy/ts-types-platform/dist/endUser/EndUserIdentityId';

import { CustomFields } from '../customField/CustomFields';
import { ChatSDKVariable } from '../env/ChatSDKVariable';
import { SDKVariableStorage } from '../env/getEnvVariable';
import { ChatSDKError } from '../error/ChatSDKError';
import { ChatEventData } from '../event/ChatEvent';
import { sendChatEvent } from '../event/sendChatEvent';
import { WebSocketClient } from '../sockets/WebSocketClient';
import { generateId } from '../utils/generateId';
import { isUndefined } from '../utils/is';

import { createSetCustomerCustomFieldsPayloadData } from './setCustomerCustomFieldsPayload';

export class Customer {
  websocketClient: WebSocketClient | null;

  constructor(
    id: CustomerIdentityIdOnExternalPlatform,
    name: string | undefined,
    websocketClient: WebSocketClient | null,
  ) {
    this.websocketClient = websocketClient;
    Customer.setId(id);
    Customer.setName(name);
  }

  static setId(id: CustomerIdentityIdOnExternalPlatform): void {
    SDKVariableStorage.set(ChatSDKVariable.CUSTOMER_ID, id);
  }

  static getId(): CustomerIdentityIdOnExternalPlatform | null {
    return SDKVariableStorage.get(ChatSDKVariable.CUSTOMER_ID, null);
  }

  static getName(): string | undefined {
    return SDKVariableStorage.get(ChatSDKVariable.CUSTOMER_NAME);
  }

  static setName(name?: string): void {
    SDKVariableStorage.set(ChatSDKVariable.CUSTOMER_NAME, name);
  }

  static getIdOrCreateNewOne(): CustomerIdentityIdOnExternalPlatform {
    let customerId = this.getId();

    if (!customerId) {
      customerId = generateId();
      this.setId(customerId);
    }

    return customerId;
  }

  public getId(): CustomerIdentityIdOnExternalPlatform {
    return Customer.getIdOrCreateNewOne();
  }

  public getName(): string | undefined {
    return Customer.getName();
  }

  public setName(name?: string): void {
    Customer.setName(name);
  }

  /**
   * Set Customer Custom fields
   * @param customFields - custom fields object
   * @example setCustomFields(\{ indentName: 'value' \})
   */
  public async setCustomFields(
    customFields: CustomFields,
  ): Promise<ChatEventData> {
    if (isUndefined(customFields)) {
      throw new ChatSDKError('Custom fields must be set');
    }

    const payloadData = createSetCustomerCustomFieldsPayloadData(customFields);

    return sendChatEvent(payloadData, this.websocketClient);
  }

  /**
   * Set Customer Custom field
   * @param name - Custom field name
   * @param value - Custom field value
   */
  public async setCustomField(
    name: string,
    value: CustomField['value'],
  ): Promise<ChatEventData> {
    if (isUndefined(name) || isUndefined(value)) {
      throw new ChatSDKError('Custom field name and value must be set');
    }

    return this.setCustomFields({ [name]: value });
  }
}
