import { ConsumerIdentityInput } from '@brandembassy/ts-types-chat/dist/awsEvents/AwsInputEvent';

import { splitName } from '../utils/splitName';

import { Customer } from './customer';

export type ConsumerIdentityPayload = ConsumerIdentityInput;

export function createCustomerIdentityPayload(
  name?: string,
): ConsumerIdentityPayload {
  const storedName = name ?? Customer.getName();

  let customerIdentityNamePayload = {};
  if (typeof storedName === 'string' && storedName.length > 0) {
    const [firstName, lastName] = splitName(storedName);
    customerIdentityNamePayload = { firstName, lastName };
  }

  return {
    idOnExternalPlatform: Customer.getIdOrCreateNewOne(),
    ...customerIdentityNamePayload,
  };
}
