import { CaseStatusChangedData } from '@brandembassy/ts-types-platform/dist/post/case/CaseStatusChangedEvent';

import { ChatEvent, ChatEventData } from '../event/ChatEvent';

export interface ContactStatusChangedChatEvent extends ChatEventData {
  data: CaseStatusChangedData;
  type: typeof ChatEvent.CONTACT_TO_ROUTING_QUEUE_ASSIGNMENT_CHANGED;
}

export function isContactToRoutingQueueAssignmentChangedEvent(
  event: ChatEventData,
): event is ContactStatusChangedChatEvent {
  return (
    event.type === ChatEvent.CONTACT_TO_ROUTING_QUEUE_ASSIGNMENT_CHANGED &&
    (event as ContactStatusChangedChatEvent).data?.case?.id !== undefined
  );
}
