import { BrandId } from '@brandembassy/ts-types-platform/dist/brand/BrandId';

import { ChatSDKVariable } from '../env/ChatSDKVariable';
import { SDKVariableStorage } from '../env/getEnvVariable';
import { ChatSDKError } from '../error/ChatSDKError';
import { isUndefined } from '../utils/is';

import { AttachmentPayload } from './createPayloadForAttachmentUpload';

export interface UploadSuccessResponse {
  fileUrl: string;
}

export interface UploadFailResponse {
  allowedFileSize: string;
  allowedFileTypes: Array<{
    description: string;
    mimeType: string;
  }>;
}

export function isUploadSuccessResponse(
  data: unknown,
): data is UploadSuccessResponse {
  return isUndefined((data as UploadSuccessResponse)?.fileUrl) === false;
}

export function isUploadFailResponse(
  data: unknown,
): data is UploadFailResponse {
  return isUndefined((data as UploadFailResponse)?.allowedFileSize) === false;
}

export async function uploadAttachment(
  brandId: BrandId,
  channelId: string,
  attachment: AttachmentPayload,
): Promise<UploadSuccessResponse | UploadFailResponse> {
  const CHANNEL_INTEGRATION_URL = SDKVariableStorage.get(
    ChatSDKVariable.ENDPOINT_CHAT,
  );
  const { url, name: fileName, mimeType } = attachment;
  const content = url.split(';base64,')[1];
  const requestBody = { content, fileName, mimeType };
  const response = await fetch(
    `${CHANNEL_INTEGRATION_URL}/chat/1.0/brand/${brandId}/channel/${channelId}/attachment`,
    {
      method: 'POST',
      body: JSON.stringify(requestBody),
      headers: {
        'Content-Type': 'application/json',
      },
    },
  );

  if (!response.ok) {
    throw new ChatSDKError(
      `Failed to upload Attachments. Status (${response.status})`,
    );
  }

  return response.json();
}
