import { BrandId } from '@brandembassy/ts-types-platform/dist/brand/BrandId';
import { ChannelId } from '@brandembassy/ts-types-platform/dist/channel/ChannelId';

import { ChatSDKVariable } from '../env/ChatSDKVariable';
import { SDKVariableStorage } from '../env/getEnvVariable';

import { isNil } from './is';

export function getBrandIdAndChannelId(): {
  brandId: BrandId;
  channelId: ChannelId;
} {
  const brandId = parseInt(SDKVariableStorage.get(ChatSDKVariable.BRAND_ID));
  const channelId = SDKVariableStorage.get(ChatSDKVariable.CHANNEL_ID);

  if (isNil(brandId) || isNaN(brandId) || isNil(channelId)) {
    throw new Error(
      `Cannot get BrandId and ChannelId from SDKVariableStorage 
      brandId (${brandId}) |
      channelId (${channelId})`,
    );
  }

  return {
    brandId,
    channelId,
  };
}
