import { SendMessageEventData } from '@brandembassy/ts-types-chat/dist/awsEvents/SendMessage';
import { BrowserFingerprint } from '@brandembassy/ts-types-chat/dist/user/BrowserFingerprint';
import { CustomField } from '@brandembassy/ts-types-platform/dist/customField/CustomField';
import { MessageId } from '@brandembassy/ts-types-platform/dist/message/MessageId';
import { MessageType } from '@brandembassy/ts-types-platform/dist/message/MessageType';
import { ThreadIdOnExternalPlatform } from '@brandembassy/ts-types-platform/dist/thread/ThreadIdOnExternalPlatform';

import { getBrowserFingerprint } from '../browserFingerprint/getBrowserFingerprint';

export const createTextMessageData = (
  messageText: string,
  messageId: MessageId,
  threadIdOnExternalPlatform: ThreadIdOnExternalPlatform,
  threadCustomFields: Array<CustomField> = [],
  customerCustomFields: Array<CustomField> = [],
  browserFingerprint: BrowserFingerprint = getBrowserFingerprint(),
): SendMessageEventData => ({
  messageContent: {
    payload: {
      text: messageText,
    },
    type: MessageType.TEXT,
  },
  browserFingerprint,
  idOnExternalPlatform: messageId,
  thread: {
    idOnExternalPlatform: threadIdOnExternalPlatform,
  },
  consumer: {
    customFields: customerCustomFields,
  },
  consumerContact: {
    customFields: threadCustomFields,
  },
  attachments: [],
});
