import { createQueryParametersAsString } from '@brandembassy/be-javascript-utils/dist/query/queryParameters';
import {
  WebSocketClient as PushUpdateWSClient,
  WebSocketClientEvent as PushUpdatesWebSocketClientEvent,
} from '@brandembassy/push-updates-sdk';
import { setupSocketConnection } from '@brandembassy/push-updates-sdk/dist/pushUpdate/pushUpdateWebSocket';
import { CloseEvent, ErrorEvent } from '@brandembassy/reconnecting-websocket';
import { BrandId } from '@brandembassy/ts-types-platform/dist/brand/BrandId';
import { ChannelId } from '@brandembassy/ts-types-platform/dist/channel/ChannelId';
import { CustomerIdentityIdOnExternalPlatform } from '@brandembassy/ts-types-platform/dist/endUser/EndUserIdentityId';

import { CHAT_SDK_VERSION } from '../env/version';

import { WebSocketClientError } from './WebSocketClientError';

export const WebSocketClientEvent = PushUpdatesWebSocketClientEvent;

function createSocketUrl(
  host: string,
  prefix: string,
  brandId: BrandId,
  channelId: ChannelId,
  consumerIdOnExternalPlatform: CustomerIdentityIdOnExternalPlatform,
): string {
  const queryParameters = {
    brandId,
    channelId,
    consumerId: consumerIdOnExternalPlatform,
    v: CHAT_SDK_VERSION,
  };

  const queryParametersAsString =
    createQueryParametersAsString(queryParameters);

  return `${host}/${prefix}?${queryParametersAsString}`;
}

export interface WebSocketClientOptions {
  forceSecureProtocol?: boolean;
  host?: string;
  onError?: (error: WebSocketClientError) => void;
  port?: string;
  prefix?: string;
}

/**
 * Websocket client
 */
export class WebSocketClient {
  private _connection: PushUpdateWSClient | null = null;

  constructor(
    private brandId: BrandId,
    private channelId: ChannelId,
    private customerId: CustomerIdentityIdOnExternalPlatform,
    private options?: WebSocketClientOptions,
    private onError?: (error: Error) => void,
  ) {
    this.connect();
  }

  /**
   * Connect websocket
   */
  connect(): void {
    const port = this.options?.port ? `:${this.options?.port}` : '';
    const host = this.options?.host ? `${this.options?.host}${port}` : '';
    const prefix = this.options?.prefix ?? '';
    const forceSecureProtocol = this.options?.forceSecureProtocol ?? true;

    const socketUrl = createSocketUrl(
      host,
      prefix,
      this.brandId,
      this.channelId,
      this.customerId,
    );

    this._connection = setupSocketConnection(socketUrl, {
      startClosed: true,
      forceSecureProtocol,
    });

    const errorHandlerBound = this._errorHandler.bind(this);
    this._connection?.addEventListener(
      WebSocketClientEvent.CLOSE,
      errorHandlerBound,
    );
    this._connection?.addEventListener(
      WebSocketClientEvent.ERROR,
      errorHandlerBound,
    );

    this._connection?.socket?.reconnect();
  }

  /**
   * Disconnect websocket
   */
  disconnect(): void {
    this._connection?.socket.close();
  }

  /**
   * Reconnect websocket
   */
  reconnect(): void {
    this._connection?.socket.reconnect();
  }

  /**
   * Send data to active connection
   * @param data - data to send
   */
  send(data: unknown): void {
    const stringData = JSON.stringify(data);
    this._connection?.send(stringData);
  }

  /**
   * Register event handler to websocket event
   * @param eventType - websocket event
   * @param handlerCallback - event handler
   */
  on(
    eventType: PushUpdatesWebSocketClientEvent,
    handlerCallback: (event: CustomEvent) => void,
  ): void {
    this._connection?.addEventListener(eventType, handlerCallback);
  }

  /**
   * Unregister event handler to websocket event
   * @param eventType - websocket event
   * @param handlerCallback - event handler
   */
  off(
    eventType: PushUpdatesWebSocketClientEvent,
    handlerCallback: (event: CustomEvent) => void,
  ): void {
    this._connection?.removeEventListener(eventType, handlerCallback);
  }

  /**
   * Handle error from event listeners with onError callback or throw error
   */
  private _errorHandler(
    event: CustomEvent<CloseEvent | ErrorEvent | Event>,
  ): void {
    const eventError = event.detail;
    let error: WebSocketClientError | undefined;

    if (eventError instanceof ErrorEvent) {
      error = new WebSocketClientError('Connection error', eventError.message);
    }
    if (eventError instanceof CloseEvent) {
      error = new WebSocketClientError('Connection closed', eventError.reason);
    }
    if (typeof error === 'undefined') {
      error = new WebSocketClientError('Unknown error', eventError.type);
    }

    if (typeof this.onError === 'function') {
      this.onError(error);
    } else {
      throw error;
    }
  }
}
