import { AccessToken } from '@brandembassy/ts-types-chat/dist/authorization/AccessToken';

import { ChatSDKVariable } from '../env/ChatSDKVariable';
import { SDKVariableStorage } from '../env/getEnvVariable';
import { isNull } from '../utils/is';

export function setAccessToken(accessToken: AccessToken): void {
  SDKVariableStorage.set(ChatSDKVariable.ACCESS_TOKEN, accessToken.token);
  SDKVariableStorage.set(
    ChatSDKVariable.ACCESS_TOKEN_EXPIRES_IN,
    String(accessToken.expiresIn),
  );
}

export function getAccessToken(): AccessToken | null {
  const token = SDKVariableStorage.get(ChatSDKVariable.ACCESS_TOKEN, null);
  const expiresIn = SDKVariableStorage.get(
    ChatSDKVariable.ACCESS_TOKEN_EXPIRES_IN,
    null,
  );

  if (isNull(token) || isNull(expiresIn)) {
    return null;
  }

  return {
    token,
    expiresIn: Number(expiresIn),
  };
}
