import { AwsResponseEventType } from '@brandembassy/ts-types-chat/dist/awsEvents/AwsEventType';
import { SetPositionInQueuePayload } from '@brandembassy/ts-types-chat/dist/awsEvents/SetPositionInQueuePayload';

import { isEmpty } from '../../utils/is';

export const isSetPositionInQueuePayload = (
  detail: unknown,
): detail is SetPositionInQueuePayload => {
  const setPostionDetail = detail as SetPositionInQueuePayload;

  return (
    Number.isInteger(setPostionDetail?.data.positionInQueue) &&
    isEmpty(setPostionDetail?.eventId) === false &&
    setPostionDetail?.eventType === AwsResponseEventType.SET_POSITION_IN_QUEUE
  );
};
